.remote,
.remote::after {
	transition: all 0.3s;
}

.remote {
  cursor: pointer;
  background: none;
  border: 2px solid #fff;
  border-radius: 5px;
  color: #fff;
  font-size: 22px;
  margin-top: 22px;
  padding: 14px 22px 14px 22px;
  position: relative;
  text-transform: uppercase;
  display: inline-block;
}

.remote::before,
.remote::after {
  background: #fff;
  content: '';
  position: absolute;
  z-index: -1;
}

.remote:hover {
  color: red;
}

/* .remote 5 */
.remote {
  overflow: hidden;
}

.remote::after {
  /*background-color: #f00;*/
  height: 100%;
  left: -35%;
  top: 0;
  transform: skew(50deg);
  transition-duration: 0.6s;
  transform-origin: top left;
  width: 0;
}

.remote:hover:after {
  height: 100%;
  width: 135%;
}
